
@use '../utils' as *;

/*----------------------------------------*/
/*  2.12 Shortcodes
/*----------------------------------------*/

// Text color
.gradient-text-1 {
	background: linear-gradient(90deg, rgb(251, 83, 67) 0%, rgb(5, 157, 255) 100%);
	color: var(--bd-gradient-2);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.gradient-text-2 {
	background: linear-gradient(92deg, #674AD9 0%, #FB5141 100%);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.gradient-text-3 {
	background: linear-gradient(90deg, #674AD9 0%, #FB5141 100%);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
.text-color-1 {
	color: var(--bd-theme-primary);
}
.text-color-2 {
	color: var(--bd-text-secondary);
}
.text-paragraph {
	color: var(--bd-text-body);
}
.bd-text-white {
	color: var(--bd-common-white);
}
// Rounded Style
.s-rounded-20 {
	border-radius: 20px;
	@media #{$xs, $sm, $md} {
		border-radius: 0;
	}
}
// Small badge
.badge__card {
	font-size: 12px;
	font-weight: 500;
	text-transform: uppercase;
	background: rgba(251, 81, 65, 1);
	display: inline-block;
	padding: 4px 4px;
	border-radius: 2px;
	color: var(--bd-common-white);
	line-height: 1;
}

// Section rounded
.section-is-rounded {
	margin: 0 40px;
	border-radius: 40px;
	@media #{$xxl} {
	  margin: 0 20px;
	}
	@media #{$xs, $sm, $md, $lg} {
	  margin: 0px;
	  border-radius: 0px;
	}
}
.section-rounded-60 {
	margin: 0 60px;
	border-radius: 20px;
	@media #{$xs, $sm, $md, $lg} {
	margin: 0px;
	border-radius: 0px;
	}
}