@use '../utils' as *;

/*----------------------------------------*/
/*  2.2 Buttons
/*----------------------------------------*/

// Sub button
.sub__btn {
    height: 45px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 40px;
    color: var(--bd-text-primary);
    text-transform: uppercase;
    padding: 0 25px;
    font-weight: var(--bd-fw-sbold);
    position: relative;
    z-index: 5;
    background: linear-gradient(90deg, rgba(87, 63, 234, 0.2) 0%, rgba(161, 145, 255, 0.2) 100%);
    font-size: 15px;
    &:before {
        position: absolute;
        content: "";
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, rgba(87, 63, 234, 0.20) 0%, rgba(161, 145, 255, 0.20) 100%);
        border-radius: 40px;
        z-index: -1;
        opacity: 0;
        -webkit-transition: all 0.5s ease-out 0s;
        -moz-transition: all 0.5s ease-out 0s;
        -ms-transition: all 0.5s ease-out 0s;
        -o-transition: all 0.5s ease-out 0s;
    }

    &:hover {
        color: var(--bd-common-white);

        &::before {
            opacity: 1;
            background: linear-gradient(90deg, rgba(87, 63, 234, 0.20) 0%, rgba(161, 145, 255, 1) 100%);
        }
    }
    &.sm {
        height: 60px;
        padding: 0 35px;
        @include light {
            color: var(--bd-common-white);
        }
    }
}
// Gradient button
.bd-gradient-btn {
    font-size: 15px;
    color: var(--bd-common-white);
    text-transform: uppercase;
    font-weight: var(--bd-fw-sbold);
    padding: 0px 35px;
    @include inline-flex();
    justify-content: center;
    align-items: center;
    gap: 6px;
    height: 60px;
    @include border-radius(30px);
    position: relative;
    -webkit-transition: all 0.5s ease-out 0s;
    -moz-transition: all 0.5s ease-out 0s;
    -ms-transition: all 0.5s ease-out 0s;
    -o-transition: all 0.5s ease-out 0s;
    z-index: 5;
    background-position: 100% 100%;
    background-size: 300% 100%;
    background-image: var(--bd-gradient-1);
    &:focus {
        color: var(--bd-common-white);
    }

    span {
        i {
            position: relative;
            top: 1px;
        }
    }

    &:hover {
        color: var(--bd-common-white);
        background-position: 0 100%;
    }
}
// Gradient button size
.bd-gradient-btn {
    &.btn-xs {
        height: 50px;
        padding: 0 25px;
	}
	&.btn-sm {
        height: 55px;
        padding: 0 30px;
	}
    &.btn-lg {
        height: 65px;
        border-radius: 50px;
        padding: 0 40px;
	}
    &.btn-xl {
        height: 75px;
        border-radius: 50px;
        padding: 0 45px;
	}
	&.btn-xxl {
        height: 85px;
        border-radius: 50px;
        padding: 0 60px;
	}
}
.bd-gradient-btn-white {
	font-size: 15px;
    display: inline-flex;
	align-items: center;
	justify-content: center;
	font-weight: var(--bd-fw-sbold);
	height: 60px;
	@include border-radius(30px);
	position: relative;
	overflow: hidden;
    padding: 0 35px;
	text-transform: uppercase;
    color: var(--bd-heading-primary);
	background-color: var(--bd-common-white);
	z-index: 5;
    span {
        i {
            margin-left: 8px;
        }
    }
	&:hover {
		background-color: var(--bd-heading-primary);
		color: var(--bd-common-white);
	}
}

// Common button
.bd-btn {
	font-size: 15px;
    display: inline-flex;
	align-items: center;
	justify-content: center;
	font-weight: var(--bd-fw-sbold);
	height: 60px;
	@include border-radius(30px);
	position: relative;
	overflow: hidden;
    padding: 0 35px;
	text-transform: uppercase;
    background-color: var(--bd-theme-primary);
    color: var(--bd-common-white);
	z-index: 5;
    span {
        i {
            margin-left: 8px;
        }
    }
	&:hover {
        background: var(--bd-theme-secondary);
        color: var(--bd-common-white);
	}
    &.btn-secondary {
		background: var(--bd-theme-secondary);
        &:hover {
            background-color: var(--bd-theme-primary);
        }
	}
	&.btn-tertiary {
		background: var(--bd-theme-tertiary);
        &:hover {
            background-color: var(--bd-theme-primary);
        }
	}
	&.btn-quaternary {
		background: var(--bd-theme-quaternary);
        &:hover {
            background-color: var(--bd-theme-primary);
        }
	}
}

// Text btn
.text__link {
    display: inline-flex;
    align-items: center;
    gap: 7px;
    color: var(--bd-common-white);
    @include light {
        color: var(--bd-text-primary);
    }
    position: relative;
    &::before {
        position: absolute;
        content: "";
        left: auto;
        bottom: -2px;
        background: currentColor;
        width: 0;
        height: 2px;
        transition: 0.3s;
        right: 0;
    }
    &:hover {
        color: var(--bd-theme-primary);
        &::before {
            width: 100%;
            left: 0;
            right: auto;
        }
    }
}
// Btn Group
.btn__group {
    display: flex;
    align-items: center;
    gap: 20px 20px;
    flex-wrap: wrap;
    margin-top: 50px;
}
.button__group {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	gap: 15px;
}
// Play btn
.play__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    background: rgb(33, 33, 33);
    font-size: 20px;
    @include border-radius(50%);
    span {
        i {
            color: var(--bd-common-white);
        }
    }
    &:hover {
        background-color: var(--bd-theme-primary);
    }
}

// Login btn
.login__gamil-btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    height: 60px;
    background: rgb(66, 133, 244);
    @include border-radius(6px);
    width: 100%;
    font-size: 15px;
    color: var(--bd-common-white);
    font-weight: var(--bd-fw-sbold);
    text-transform: uppercase;
}

// Pricing btn
.pricing__btn{
    font-size: 16px;
    color: var(--bd-text-primary);
	font-weight: var(--bd-fw-medium);
    padding: 0px 40px;
	@include inline-flex();
	align-items: center;
    justify-content: center;
    gap: 10px;
	height: 60px;
    @extend %bd-transition;
    position: relative;
    z-index: 5;
    &::before,
    &:after {
        content: "";
        position: absolute;
        border-radius: 99px;
    }
    &:hover {
        color: var(--bd-common-white);
    }
}
.pricing__btn{
    font-size: 15px;
    color: var(--bd-text-primary);
	font-weight: var(--bd-fw-medium);
    padding: 0px 40px;
	@include inline-flex();
    @include border-radius(30px);
	align-items: center;
    justify-content: center;
    gap: 10px;
	height: 60px;
    @extend %bd-transition;
    position: relative;
    z-index: 5;
    border: 2px solid var(--bd-theme-primary);
    &:hover {
        background-color: var(--bd-theme-primary);
    }
}

.pricing__btn-white {
    font-size: 15px;
    color: var(--bd-common-white);
	font-weight: var(--bd-fw-medium);
    text-transform: uppercase;
    padding: 0px 40px;
	@include inline-flex();
    @include border-radius(30px);
	align-items: center;
    justify-content: center;
    gap: 10px;
	height: 60px;
    @extend %bd-transition;
    position: relative;
    z-index: 5;
    border: 2px solid var(--bd-common-white);
    &:hover {
        color: var(--bd-heading-primary);
        border-color: transparent;
        background-color: var(--bd-common-white);
    }
}
